<script lang="coffeescript">

  import Calendar from "../../components/Date/DatePicker/Calendar.svelte"

  import tippy from 'tippy.js'


  selectedDate = Date.create().format("{yyyy}-{MM}-{dd}")
  setDay = (e)->
    console.log "set day"



  popover = (element, content) ->
    tippyObject = undefined
    component = undefined


    tippyOptions =
      distance: '-20px'
      theme: "datepicker"
      trigger: "click"
      interactive: true


    if (typeof content == 'function')

      tippyOptions.onCreate = (instance) ->
        component = new content(target: instance.popper.querySelector('.tippy-content'))
        return

    else
      tippyOptions.content = content
    if Object.keys(tippyOptions).length == 0
      tippyObject = tippy(element)
    else
      tippyObject = tippy(element, tippyOptions)
    return
    {
      update: (params) ->
        #if component
        #  component.$set params.props
        return
      destroy: ->
        tippyObject.destroy()
        if component
          debugger
          component.$destroy()
        return

    }




</script>


