<script lang="coffeescript">
  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()

  export is_hidden = undefined
  export has_link = false

  select = (action)->
    dispatch("selected",action)


</script>
<ul role="menu">
  {#if has_link}
  <li><button type="button" on:click={select("openLink")}>Open Link in New Window</button></li>
  <li><button type="button" on:click={select("copyLink")}>Copy Link</button></li>
  <hr/>
  {/if}
  <li><button type="button" on:click={select("addChild")} disabled>Nest New Item</button></li>
  <li><button type="button" on:click={select("addBelow")} disabled>Insert New Item Below</button></li>
  <hr/>
  <li><button type="button" on:click={select("copy")}>Copy Text to Clipboard</button></li>
  
  <li><button type="button" on:click={select("hide")}>
    {#if is_hidden}
    Unhide
    {:else}  
    Hide
    {/if} Text
  </button></li>
  <hr/>
  <li>
    <button type="button" on:click={select("clone")} disabled>Clone</button>
  </li>  
  <li>
    <button type="button" on:click={select("delete")}>Delete</button>
  </li>
  
</ul>
