<script lang="coffeescript">

  import { Row, RowCell } from "../../../components/Flex"
  import Menu from "../../../components/Overlay/Menu/ComposedMenu.svelte"

  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()

  export index = undefined
  export item = undefined
  export listId = undefined

  export sameListOnly = false
  export showMenu = true



  indentButtonRef = undefined

  draggableEl = undefined
  draggable = false

  isOver = false
  isInvalid = false

  checkTypesForListId = (types)->
    types.find (t)->
      return t.startsWith("listid:")

  start = (e)->
    item["listId"] = listId
    item["index"] = index || null
    e.dataTransfer.setData("text/plain", JSON.stringify(item))
    e.dataTransfer.setData("listid:#{listId}", JSON.stringify(item))



  over = (e)->
    e.preventDefault()
    isOver = true
    unless !sameListOnly
      draggedListId = checkTypesForListId(e.dataTransfer.types)
      if draggedListId?
        isInvalid = !(listId == draggedListId.replace("listid:",""))
      else
        isInvalid = true

  leave = (e)->
    draggable = false
    isOver = false
    isInvalid = false

  drop = (e)->
    isOver = false
    isInvalid = false
    draggable = false
    e.preventDefault()
    dragItem = JSON.parse(e.dataTransfer.getData("text/plain"))
    dropped = true
    unless !sameListOnly
      dropped = (listId == dragItem.listId)
    if dropped
      dispatch "nest",
        dragItem: dragItem
        dragIndex: dragItem.index
        dragListId: dragItem.listId
        dropItem: item
        dropIndex: index
        dropListId: listId



  mouseup = ()->
    draggable = false
    isInvalid = false

  mousedown = ()->
    draggable = true

  getWidth = (el)->
    if el?
      el.clientWidth - 48
    else
      0


  toggleExpanded = ()->
    item.is_expanded_raar = !item.is_expanded_radar
    dispatch "update", {uuid: item.uuid, is_expanded_radar: item.is_expanded_radar }

  `$: draggableWidth = getWidth(draggableEl) `

</script>


<div
  item-draggable
  data-id={item.id}
  class:over={isOver}
  class:invalid={isInvalid}
  {draggable}
  on:drop={drop}
  on:dragover={over}
  on:dragstart={start}
  on:dragleave={leave}
  bind:this={draggableEl}
>

      <div flex="initial" handle on:mouseup={mouseup(item)} on:mousedown={mousedown(item)}>
        <slot name="grabber">
          <svg class="handle" xmlns="http://www.w3.org/2000/svg" width="24" height="24" version="1.1" viewBox="0 0 24 24"><path d="M9,3H11V5H9V3M13,3H15V5H13V3M9,7H11V9H9V7M13,7H15V9H13V7M9,11H11V13H9V11M13,11H15V13H13V11M9,15H11V17H9V15M13,15H15V17H13V15M9,19H11V21H9V19M13,19H15V21H13V19Z"/></svg>
        </slot>
      </div>
      <div indent-menu>
      {#if item.radar_items.length > 0}
        <button icon on:click={toggleExpanded}>
          {#if item.is_expanded_radar}
            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path class="fill" d="M7 10l5 5 5-5H7z"/></svg>
          {:else}
            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path class="fill" d="M10 17l5-5-5-5v10z"/></svg>
          {/if}
        </button>
      {/if}
      </div>
      <div flex class="slot"><slot item={item} index={index}/></div>
      <div flex="initial" draggable-item-menu style="padding-left: 24px;padding-right: 4px">
        <slot name="menu"/>
      </div>

</div>
<style global lang="sass">
  // global to ensure child menus inherit styles
  [item-draggable]

    border-radius: 3px
    &[draggable="true"]


    box-sizing: border-box
    display: flex

    padding: 4px 0px

    flex-direction: row

    width: calc(100% + 56px)
    max-width: calc(100% + 56px)

    .slot
      width: calc(100% - 96px)
      max-width: calc(100% - 96px)

    margin-left: -24px


    [handle], [draggable-item-menu]
      width: 24px

    [indent-menu]
      width: 24px
      svg, svg.menu
        display: inline

    [handle]:hover
      cursor: grab
    &.over
      background: #dddddd
    &.invalid
      background: var(--dark-red) !important
    &:hover
      background: #fcfcfc
      [handle], [indent-menu], [draggable-item-menu]
        svg, svg.menu
          display: inline

      [handle]
        svg
          path
            fill: var(--light-gray)
      [draggable-item-menu]
        svg
          path
            fill: var(--dark-gray)
        &:hover
          svg
            path
              fill: var(--blue)

      [indent-menu]
        button[icon]
          padding: 3px 0px
        svg
          path.fill
            fill: var(--light-gray)
        &:hover
          svg
            path
              fill: var(--blue)


    & > [flex]
      flex: 1
      box-sizing: border-box
      max-height: 100%


    & > [flex=initial]
      flex: 0 1 auto

    & > [flex=auto]
      flex: 1 1 auto

    & > [flex=none]
      flex: 0 0 auto


    & > [flex=noshrink]
      flex: 1 0 auto


    & > [flex=nogrow]
      flex: 0 1 auto



</style>
