      color: var(--blue)

  button[pushshortcut]      
    padding: 1px 12px
    font-size: 16px
    font-weight: bold

  [monthname]
    font-weight: bold
    text-align: center
    vertical-align:middle
    padding: 0px
    font-size: 16px
    text-transform: uppercase



  tr[weekdays]
    td
      text-align: center
      padding: 3px 0px

  tbody
    td
      text-align: center
      vertical-align: middle
      height: 32px
      width: 32px
      font-weight: bold

  [month=previous], [month=next]
    button
      color: var(--lighter-gray)



  thead
    button
      padding: 0px
      background: none
      border: none
      &:hover
        color: blue
  tbody
    td[events]
      background: tan
    td[events="0"]
      background: none

    button
      background: none
      text-align: center
      vertical-align: middle
      height: 100%
      width: 100%
      border-radius: var(--corner-m)


  [today="true"]
    button
      border: 1px solid var(--lightest-blue)

  [selected="true"]
    button
      background-color: var(--blue)
      color: var(--white)


  [skip-icon]
    path
      fill: var(--light-blue)



</style>
