<script lang="coffeescript">
  # /lists
  # List Index


  import {push, pop, replace, location} from "svelte-spa-router"


  # Data Items

  import RootItemList from '../../helpers/root_item_list.js'

  # Common Layout Components

  import PageContent from '../_Common/PageContent.svelte'
  import PageHeader from '../_Common/PageHeader.svelte'
  import Indent from "../_Common/Indent.svelte"

  # List Item Components

  import DraggableItem from "./Sections/FlatNestable/NestableItem.svelte"
  import DroppableDivider from "./Sections/FlatNestable/DroppableDivider.svelte"



  import QuickEntry from "../_Common/QuickEntry/GenericItem.svelte"
  import Menu from "../_Common/Menus/List.svelte"
  import InlineInput from '../../components/Form/InlineInput/InlineInput.svelte'
  import ProgressPopover from '../_Common/Popovers/Progress.svelte'
  import DueDatePopover from '../_Common/Popovers/DueDate.svelte'


  # Local Vars

  export list = []
  export listId = uuid()

  export debug = false

  # List Model

  resourceList = new RootItemList

  refreshList = (items)->
    list = items

  resourceList.select refreshList # load the list from PouchDB





  # jumps

  itemUrl = "#/lists/" # links and redirects


  goToItem = (item)->
    push "#/lists/#{item.id}"

  handleMenuSelection = (e)->
    console.error "menu selection"
    console.log e.detail

  toggleFavorite = (rootItemList)->
    console.log "toggle Favorite"
    console.log rootItemList

    rootItemList.is_favorite = !rootItemList.is_favorite

    list = list

    updateRootItem
      detail:
        id: rootItemList.id
        is_favorite: rootItemList.is_favorite

  # CRUD


  addRootItem = (e)->
    scb = (inserted)->
      push "/lists/#{inserted.id}" # redirect to list page
    resourceList.insert e.detail, scb, null, null


  updateRootItem = (e)->
    console.error "updateRootItem"
    console.log e.detail
    scb = ()->
      resourceList.select refreshList
    ecb = (err)->
      # refreshList()
    resourceList.patch e.detail, scb, ecb, null

  destroy = (e)->

    ecb = (e)->
      console.log "destroy error"
      console.log e

    resourceList.destroy e.detail._id, refreshList, ecb


  # UI

  sort = (list)->
    if list?
      return list.sortBy "index"
    else
      []


  # DRAG AND DROP

  dropHandler = (e)->
    refresh = false
    #console.log "drop event, root list"
    #console.log e.detail

    scb = (refreshedList)->
      # console.log "drop success"
      refreshList refreshedList

    ecb = (e)->
      console.log "drop error"
      console.log e


    resourceList.drop e.detail, scb, ecb

  inlineInputStyle = ":hover {font-weight: bold}" # braces cause problem when inline


  `$: sortedlist = sort(list)`

</script>

<style lang="sass">
  [type="list-item"]
    width: 100%
    max-width: 100%
    [present]
      /* display: none */
    [present="true"]
      display: flex
    [main]

    &:hover
      [present]
        display: flex

  header
    h1
      font-size: 36px
      color: var(--darkest-blue)
  section
    margin: 0px 56px

</style>

<svelte:head>
  <title>Radar :: Lists</title>
</svelte:head>



<PageContent>
  <PageHeader>Lists</PageHeader>
  <section>
    {#if list && list.length}
      <div>
        <DroppableDivider index={0} {listId} on:drop={dropHandler} />
        {#each sortedlist as item, index}
          <DraggableItem item={item} index={index} {listId} on:drop={dropHandler} on:update={updateRootItem}>
            <div layout="row" type="list-item">
              <Indent level={(item.level || 0).clamp(0,5)}/>

              <div flex="initial" on:click={toggleFavorite(item)}>
                {#if item.is_favorite}
                  <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M12,17.27L18.18,21L16.54,13.97L22,9.24L14.81,8.62L12,2L9.19,8.62L2,9.24L7.45,13.97L5.82,21L12,17.27Z"/></svg>
                {:else}
                  <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path fill="none" d="M0 0h24v24H0V0z"/><path d="M22 9.24l-7.19-.62L12 2 9.19 8.63 2 9.24l5.46 4.73L5.82 21 12 17.27 18.18 21l-1.63-7.03L22 9.24zM12 15.4l-3.76 2.27 1-4.28-3.32-2.88 4.38-.38L12 6.1l1.71 4.04 4.38.38-3.32 2.88 1 4.28L12 15.4z"/></svg>
                {/if}
              </div>
              <div flex="initial" section="progress">
                <ProgressPopover {item} bind:value={item.progress} on:update={updateRootItem} on:open on:close idAttribute="id"/>
              </div>

              <div flex ellipsis>
                <InlineInput bind:value={item.name} on:update={updateRootItem( { detail: { id: item._id, name: item.name } })} on:clicked={goToItem(item)} {inlineInputStyle} let:label>
                  <span ellipsis>
                    <a href="{itemUrl}{item.id}" on:click|preventDefault><em>{label || "No Name"}</em></a>&nbsp;

                  </span>
                </InlineInput>
                {#if debug}
                <div>_ID: #{item._id}</div>
                <div>ID: #{item.id}</div>
                <div>Index: #{item.index}</div>
                <div>Level: #{item.level}</div>
                <div>Parent Id: #{item.parent_id}</div>
                {/if}
              </div>
              <div flex="initial">
                <div layout="row">


                  <!-- due date menu -->
                  <div flex="initial" present={item.due_date != null}>
                    <DueDatePopover {item} value={item.due_date} on:update={updateRootItem} idAttribute="id" on:open on:close/>
                  </div>

                </div>
              </div>

            </div>

            <div slot="menu">
              <Menu {item} on:select={updateRootItem} on:destroy={destroy}/>

            </div>
          </DraggableItem>
          <DroppableDivider {item} index={index + 1} {listId} on:drop={dropHandler} />
        {/each}
      </div>
    {/if}
    <QuickEntry on:add={addRootItem} stubPlaceholder="Add List" />
  </section>
</PageContent>
