<script lang="coffeescript">

  import { createEventDispatcher } from "svelte"
  import { slide } from "svelte/transition"
  import { sineIn, sineOut } from 'svelte/easing'
  import QuickEntry from "../../../_Common/QuickEntry/GenericItem.svelte"
  dispatch = createEventDispatcher()

  import Indent from "../Indent.svelte"


  export index = undefined
  export indentation = 0
  export item = { is_divider: true }
  export listId = undefined
  export firstItem = false




  isOver = false
  isHover = false
  isInvalid = false

  export isExpanded = false

  over = (e)->
    e.preventDefault()
    e.stopPropagation()
    isOver = true

    isInvalid = false

    # console.error "OVER"

    #if isInvalid
    #  return e.preventDefault()
    #console.log "return is invalid: #{!isInvalid}"
    # return !isInvalid

  hover = (e)->
    e.preventDefault()
    e.stopPropagation()
    unless isOver
      isHover = true
      # console.error "HOVER"

  unhover = (e)->
    e.preventDefault()
    e.stopPropagation()
    unless isOver
      # console.log "UNHOVER"
      isHover = false

  leave = (e)->
    draggable = false
    isOver = false
    isInvalid = false
    # console.error "LEAVE"
    # isHover = false

  add = (e)->
    e.stopPropagation()
    toIndex = item.index
    if firstItem
      if index # takes priority
        toIndex = index
      else
        toIndex = 0

    isHover = false
    isExpanded = false
    console.error "divider add"
    console.debug { ancestry: item.ancestry, index: toIndex }
    dispatch "add", { ancestry: item.ancestry, index: toIndex }

  ###

  Drop Cases

  1. Drop to Reorder

  2. Drop to Nest and Reorder


  If target ancestry is the same as origin, then it''s a reorder 

  If target ancestry is different from origin, then it''s a nested move

  ###

  drop = (e)->
    isOver = false
    isInvalid = false
    draggable = false


    eventType = "drop"
  

    ancestry = item.ancestry.clone()

    # ancestry.pop()
    
    console.log "NestableDivider.drop item"
    console.debug item
    dropItem = { ancestry: ancestry, index: item.index }

    console.log "dropItem"
    console.log dropItem


    #console.log "NestableDivider.svelte"

    e.preventDefault()
    dragItem = JSON.parse(e.dataTransfer.getData("text/plain"))

    #console.log item

    #console.log "--------------------------------"

    unless firstItem
      if item.list_items.length > 0
        console.log "------------------------------ DIVIDER DROP"
        console.log "dragitem"
        console.debug dragItem
        console.log "dropitem"
        console.log item


        console.log "item.list_items.length #{item.list_items.length} :: nest"
        eventType = "drop"
        item["to_index"] = 0
      else

        console.log "DROP ITEM #{item.name} index: #{item.index}"
        item["to_index"] = item.index + 1

      # console.log "evntType: #{eventType}"

      dropped = true

      unless Object.has(item, "is_divider")
        item["is_divider"] = true

      #console.log "DroppableDivider - drop"
      #console.log item

    else # handle first item
      console.error "FRIST ITEM"
      item["to_index"] = 0

      console.log dragItem
      console.log "eventType #{eventType}"
      dropped = true

    if dropped
      dispatch "drop",
        divider: true
        dragItem: dragItem
        dragIndex: dragItem.index
        dragListId: dragItem.listId
        dropItem: dropItem # item
        dropIndex: index + 1
        dropListId: listId



  toggleExpanded = ()->
    isExpanded = !isExpanded

  collapse = (e)->
    e.stopPropagation()
    isExpanded = false

</script>

<div
  divider
  layout="row"
  class:over={isOver}
  class:hover={isHover}
  on:drop={drop}
  on:dragover={over}
  on:dragleave={leave}
    on:mouseenter={hover}
    on:mouseleave={unhover}
    class:expanded={isExpanded ? "expanded" : false}
>
  <div spacer flex="initial"/>
  <Indent level={indentation} flex="initial"/>
  <div flex
    class:expanded={isExpanded ? "expanded" : false}
    class:over={isOver}
    on:click={  toggleExpanded}
    class="indented"
  >
    
    {#if isExpanded}
      <div class="add-divider"
        in:slide="{{delay: 150, duration: 300, easing: sineIn }}"
        class:invalid={isInvalid}
         
         layout="row"
      >
        <button layout="row" on:click={add}>
          <div flex="initial" icon><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M12,20C7.59,20 4,16.41 4,12C4,7.59 7.59,4 12,4C16.41,4 20,7.59 20,12C20,16.41 16.41,20 12,20M12,2A10,10 0 0,0 2,12A10,10 0 0,0 12,22A10,10 0 0,0 22,12A10,10 0 0,0 12,2M13,7H11V11H7V13H11V17H13V13H17V11H13V7Z"/></svg></div>
          <div flex>Click to Add New List Item</div>          
        </button>
        <button layout="row"  on:click={collapse}>
          <div flex="initial" icon><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" version="1.1" viewBox="0 0 24 24"><path d="M12,2A10,10 0 0,1 22,12A10,10 0 0,1 12,22A10,10 0 0,1 2,12A10,10 0 0,1 12,2M12,4A8,8 0 0,0 4,12C4,13.85 4.63,15.55 5.68,16.91L16.91,5.68C15.55,4.63 13.85,4 12,4M12,20A8,8 0 0,0 20,12C20,10.15 19.37,8.45 18.32,7.09L7.09,18.32C8.45,19.37 10.15,20 12,20Z"/></svg></div>
          <div flex>Cancel</div>                  
        </button>

      </div>
    {/if}
      <div class="droptarget"
        class:invalid={isInvalid}>
        Drop to Move List Item Here
      </div>


  </div>
</div>


<style lang="sass">



  div[divider]
    // margin: 2px 0px
    height: 8px
    transition: height 0.15s ease-out
    border-radius: 4px

    .indented
      border-radius: 4px
      background: transparent


    &:hover
      .indented
        background: #f1f1f1
    
    &.over
      height: 24px
      transition: height 0.15s ease-in
      .indented
        background: #f1f1f1
    
    &.expanded
      height: 40px
      transition: height 0.15s ease-in


    .expanded
      padding: 8px 0px
      border-top: 8px solid transparent
      border-bottom: 8px solid transparent

      height: 40px

      .indented
        background: transparent
    //background: var(--lightest-blue)


    // drag and drop
    .droptarget
      height: 100%
      color: transparent
      line-height: 24px

    .indented.over
      .droptarget
        padding: 0px 8px
        color: var(--light-gray) !important
        background: transparent
        border-radius: 4px

    .indented.hover



    .add-divider
      text-align: left
      border-radius: 3px
      line-height: 24px
      vertical-align: middle
      [icon]
        padding-right: 8px
        svg
          fill: var(--green)
        color: var(--green)
      // background: var(--green) !important
    // hover state






    &.invalid
      .placeholder
        background: var(--red) !important




    div[spacer] // accommodate expand collapse icon
      width: 24px

</style>
