<script lang="coffeescript">

  import { onMount, createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()



  import NestableItem from "./Nestable/NestableItem.svelte"
  import NestableDivider from "./Nestable/NestableDivider.svelte"



  import InlineInput from '../../../components/Form/InlineInput/InlineInput.svelte'

  import Menu from '../../_Common/Menus/ListItem.svelte'

  import ProgressPopover from '../../_Common/Popovers/Progress.svelte'
  import DueDatePopover from '../../_Common/Popovers/DueDate.svelte'
  import ListItem from "../../../helpers/list_item"




  export debug = false
  export list = []



  message = ""

  style = ":hover {font-weight: bold}"

  goToItem = (childItem)->
    location.href="#/lists/#{childItem.id}"

  updateListItem = (e)->
    dispatch "update", e.detail

  destroyDispatcher = (e)->
    dispatch "destroy", e.detail
    showDeleteConfirmation = false

  # drag and drop

  dropDispatcher = (e)->
    dispatch "drop", e.detail


  nestDispatcher = (e)->
    dispatch "nest", e.detail

  addDispatcher = (e)->
    console.log "addDispatcher"
    dispatch "add", e.detail

</script>
{#if (list.type != "RootItem") && (list.list_items.length > 0)}
  <NestableDivider item={{ ancestry: list.ancestry.clone().append(list.uuid), index: -1} } indentation={list.ancestry.length + 1}  on:drop={dropDispatcher} on:nest={nestDispatcher} firstItem={true} on:add={addDispatcher} index={-1}/>
{/if}
{#each list.list_items as listItem, index}


  <NestableItem item={listItem} index={index} indent={listItem.ancestry.length} on:nest={nestDispatcher} on:update={updateListItem}>
    <div layout="row" type="list-item">

      <div flex="initial" section="progress">
        <ProgressPopover item={listItem} bind:value={listItem.progress} on:update={updateListItem} on:open on:close idAttribute="uuid"/>
      </div>

      <div flex ellipsis main>
        <InlineInput bind:value={listItem.name} on:update={updateListItem({ detail: { uuid: listItem.uuid, ancestry: listItem.ancestry, name: listItem.name  } })} {style} let:label>
          {#if listItem.is_hidden}<span ellipsis>Text for this is hidden. You can edit it to see it, or unhide it using the menu.</span>
          {:else}<span ellipsis>{label || "No Name"}</span>
          {/if}
        </InlineInput>
        {#if debug }
          <div>uuid {listItem.uuid}</div>
          <div>index {listItem.index}</div>
          <div>ancestry {JSON.stringify(listItem.ancestry)}</div>
          <div>children: {listItem.list_items.length}</div>
        {/if}
      </div>
      <div flex="initial">
        <div layout="row">
          <!-- due date menu -->
          <div flex="initial" section="due-date" present={listItem.due_date != null}>
            <DueDatePopover item={listItem} value={listItem.due_date} on:update={updateListItem} on:open on:close idAttribute="uuid"/>
          </div>
        </div>
      </div>

    </div>

    <div slot="menu">
      <Menu bind:item={listItem} on:update={updateListItem} on:destroy={destroyDispatcher}/>
    </div>

  </NestableItem>
  {#if (listItem.list_items.length == 0) || (!listItem.is_expanded)}
  <NestableDivider item={{ ancestry: listItem.ancestry.clone().append(listItem.uuid), list_items: [], index: -1}} indentation={listItem.ancestry.length + 1} on:drop={dropDispatcher} on:add={addDispatcher}/>
  {/if}
  <!-- children -->
  {#if listItem.is_expanded }
    <svelte:self bind:list={listItem}  on:update={updateListItem} on:drop={dropDispatcher} on:nest={nestDispatcher} on:destroy={destroyDispatcher} on:add={addDispatcher}/>
  {/if}
  <!-- don't show when last item of root -->
  {#if !((list.type == "RootItem") && (index == (list.list_items.length - 1)))}
    <NestableDivider item={listItem} indentation={listItem.ancestry.length} on:drop={dropDispatcher} on:nest={nestDispatcher} on:add={addDispatcher}/>
    <div style="height: 4px;"/>
  {/if}


{/each}



<style lang="sass">
  [type="list-item"]
    width: 100%
    max-width: 100%
    [section="progress"]
      padding-right: 8px
    [present]
      /* display: none - remove from dom instead */
    [present="true"]
      display: flex
    [main]

    &:hover
      [present]
        display: flex

</style>
