    originalItemReference = found.reference_from_ancestry(change.ancestry)
    originalItemIndex = originalItemReference.list_items.findIndex({uuid: change.uuid})
    originalItem = originalItemReference.list_items[originalItemIndex]


    console.debug change

    Object.merge(originalItem, change)

    scb = (items)->


    console.error "CHANGED ITEM"
    console.debug found

    resource.patch found.pouchify(), getRootItems


    #refreshAndUpdate()




  radarList = (list)->
    #output = list.filter (i)->
    #  return i.that.is_ongoing() == true
    output = []
    list.forEach (i)->
      if i.has_ongoing()
